<template>
  <div class="time-list">
    <div :class="['time-list-item', time.state]">
      {{ formattedTime }}
    </div>
  </div>
</template>

<script>
export default {
  name: "TimeList",
  components: {},
  props: {
    time: { default: null }
  },
  computed: {
    formattedTime() {
      let time = this.time.time;

      let hour = (time.getHours() < 10 ? "0" : "") + time.getHours();
      let minute = (time.getMinutes() < 10 ? "0" : "") + time.getMinutes();

      return `${hour}:${minute}`;
    }
  }
};
</script>

<style lang="less">
.time-list-item {
  padding: 3px 0;
  border: 1px solid transparent;
  color: #b6b6b6;
  font-size: .75rem;
}

.available {
  color: #02cab9;
  font-weight: 700;
}
</style>